import {NextRequest} from "next/server"
import createMiddleware from "next-intl/middleware"
import {routing} from "./i18n/routing"

const intlMiddleware = createMiddleware(routing)

export default function middleware(request: NextRequest) {
    if (request.nextUrl.pathname.startsWith("/api")) return
    if (request.nextUrl.pathname.startsWith("/_next")) return
    if (request.nextUrl.pathname.startsWith("/_vercel")) return
    if (/\.(.*)$/.test(request.nextUrl.pathname)) return // Skip files

    return intlMiddleware(request)
}

export const config = {
    matcher: [
        // Match all pathnames except for
        // -  if they start with `/api`, `/_next` or `/_vercel`
        // -  the ones containing a dot (e.g. `favicon.ico`)
        "/((?!api|_next|_vercel|.*\\..*).*)",
        // However, match all pathnames within `/users`, optionally with a locale prefix
        // '/([\\w-]+)?/users/(.+)'
    ],
    // matcher: ["/((?!_next|favicon.ico).*)"], // Match all paths except static files
}
